.form-control-range {
  width: 100%;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  margin-bottom: 11px;
  border: 0;
}

.form-control-range::-webkit-slider-runnable-track {
  height: 11px;
  background: #4a4a4a;
}

.form-control-range::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 13px;
  height: 11px;
  background: #fff;
  border: 1px solid #fff;
}

.form-control-range:focus {
  outline: none;
}

.form-control-range::-moz-range-track {
  height: 11px;
  background: #4a4a4a;
  border: none;
  border-radius: 0;
}

.form-control-range::-moz-range-progress {
  height: 11px;
  background: #3c9cd6;
}

.form-control-range::-moz-range-thumb {
  width: 13px;
  height: 11px;
  background: #fff;
  border: 1px solid #fff;
  border-radius: 0;
}

.form-control-range::-moz-focus-inner {
  border: 0;
}

.form-control-range::-ms-track {
  height: 11px;
  color: transparent;
  background: transparent;
  border-color: transparent;
  border-width: 6px 0;
}

.form-control-range::-ms-fill-lower {
  background: #3c9cd6;
  border-radius: 0;
}

.form-control-range::-ms-fill-upper {
  background: #4a4a4a;
  border-radius: 0;
}

.form-control-range::-ms-thumb {
  width: 13px;
  height: 11px;
  background: #fff;
  border: 1px solid #fff;
  border-radius: 0;
}

